import React from "react";

function NoPage() {
    let error = {
        position: "absolute",
        top: "50%",
        left: "50%",
        transform: "translate(-50%, -50%)",
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        width: "75vw",
    };
    let h1 = {
        fontSize: "15vw",
        margin: "0",
        lineHeight: "5vw",
        width: "20%",
        opacity: ".5",
    };
    let label = {
        fontSize: "5vw",
        margin: "0",
    };
    let h2 = {
        width: "50%",
        fontSize: "4vw",
        color: "white",
        opacity: ".7",
    };
    return (
        <div style={error}>
            <h1 style={h1}>
                404 <label style={label}>error</label>
            </h1>
            <h2 style={h2}> WE COULDN'T FIND THIS PAGE!</h2>
        </div>
    );
}

export default NoPage;
